@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  /* overflow-y: hidden; */
}
.hideMobile {
  display: none;
}
@media (max-width: 600px) {
  .hideWeb {
    display: none;
  }
  .hideMobile {
    display: inline;
  }
}
a,
a:link,
a:visited,
a:hover,
a:active {
  color: inherit;
  text-decoration: inherit;
  font-weight: inherit;
}


.Home{background-color:#ff6961;width:100vw;height:100vh}.Home .nav{display:flex;flex-direction:column;align-items:center}.Home .nav .navItem{display:flex;justify-content:space-between;align-items:center;width:70vw;padding:30px}.Home .nav .navItem p{font-weight:lighter;color:white}.Home .nav .navItem p:hover{-webkit-text-decoration:underline solid white;text-decoration:underline solid white;cursor:pointer;line-height:10px}.Home .nav .nameSvg{width:80vw;margin-top:30vh}

.Intro{width:60vw;display:flex;justify-content:center;align-items:center}.nameSvg path:nth-child(2){stroke-dasharray:400px;stroke-dashoffset:400px;-webkit-animation:line-anim 3s ease forwards;animation:line-anim 3s ease forwards}.nameSvg path:nth-child(3){stroke-dasharray:490px;stroke-dashoffset:490px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(4){stroke-dasharray:390px;stroke-dashoffset:390px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(5){stroke-dasharray:690px;stroke-dashoffset:690px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(6){stroke-dasharray:330px;stroke-dashoffset:330px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(7){stroke-dasharray:365px;stroke-dashoffset:365px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}.nameSvg path:nth-child(8){stroke-dasharray:450px;stroke-dashoffset:450px;-webkit-animation:line-anim 4s ease forwards;animation:line-anim 4s ease forwards}@-webkit-keyframes line-anim{to{stroke-dashoffset:0}}@keyframes line-anim{to{stroke-dashoffset:0}}

.About{padding:0 100px;padding-top:15vh;background-color:#08080dd8;color:white;height:100vh}h2{font-family:"Poppins", sans-serif;font-weight:Bold 700;font-size:40px;padding:0px}hr{width:25px;height:3px;border:1px solid #ff6961;background-color:#ff6961}.socialsWrapper{padding-top:5px}.topBar{display:flex}.topBarSecond{display:flex;flex-direction:column;width:50%}.topBarFirst{width:40%}.topBarSecond-first{display:flex;padding-top:0.5em;justify-content:space-between}.topBarSecond-first li{padding-right:20px}.topBarSecond-first li:hover{-webkit-text-decoration:underline solid #ff6961;text-decoration:underline solid #ff6961;cursor:pointer}.introduction{padding:25px 0;font-family:"Share Tech", sans-serif;font-weight:lighter;line-height:20px;color:white}.skater{width:100%}.socials{padding:20px;padding-left:0px;width:5%}.learnMore hr{display:none}.learnMore:hover{cursor:pointer}.learnMore:hover hr{display:block}.learnMore .text{display:flex;align-items:center}.learnMore .text p{padding-right:15px;color:#ff6961}.learnMore .text img{width:25px;padding-top:2px}@media (max-width: 600px){.topBar{display:flex;flex-direction:column}h2{font-size:20px}.About{padding:0 50px;padding-top:100px;background-color:#08080dd8;color:white;height:100vh}.topBarSecond,.topBarFirst{width:100%}}

@media (max-width: 600px){.portfolioCardWrapper{display:block}}

.cardContent{background:url(/cchemazu/static/media/expedicom.b9208e44.jpg) no-repeat center center fixed;background-size:cover;background-attachment:local;width:200px;height:200px;display:flex;flex-direction:column;align-items:center;justify-content:center}.cardContent:hover::after{content:" ";display:none}.cardContent:after{content:"";position:absolute;width:200px;height:200px;background-color:black;opacity:0.6;z-index:5px}.cardContent h3,.cardContent button{z-index:100}.cardContent h3{font-size:1.5rem;font-family:"Share Tech", sans-serif;font-weight:bold;line-height:2rem;letter-spacing:0.1em}

.Services{display:flex;flex-direction:column;height:100vh;width:100vw;background-color:#08080dd8}.display{background-color:#fff;padding:2rem;padding-left:4rem}p.skill,p.project{padding:0px;margin:0px}.skillSet{padding:2rem;display:flex;justify-content:space-evenly}.skillSet .singleSkill{color:white;background-color:#ff6961;padding:0.5rem;display:flex;min-width:92px;min-height:64px;height:8.5vw;width:20vw}.skillSet .skillSpacer{width:5vh}@media (max-width: 600px){.Services{flex-direction:row-reverse}.display{flex-basis:70%;padding:1rem;padding-top:4rem}.skillSet{flex-basis:30%;padding-bottom:30vh;display:flex;flex-direction:column}.skillSet .singleSkill{flex-direction:column}.skillSet .skillSpacer{height:3vh}}

